.homeButton{
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.3s;
       transition-duration: 0.3s;
    border-style: none;
    background-color:transparent;
    color: #181818;
    border-radius: 4px;
    font-size: 20px;
    padding: 15px 25px;
    margin: 10px 10px;
    border:solid;
    border-width: thin;
    text-decoration: none;
}

.homeButton:hover {
    background-color: rgb(120, 121, 120); /* Green */
    color: white;
    cursor: pointer;
}
.buttonContainer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.homeContainer{
    display: block;
    margin:auto;
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
}
